import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    letter:"A",
    city:localStorage.getItem("city") || "咸宁"
  },
  mutations: {
    changeLetter(state,letter){
      // console.log(letter)
      state.letter=letter
    },
    changeCity(state,city){
      state.city=city
    }
  },
  actions: {
    /* changeLetter(ctx,letter){
      // console.log(letter);
      ctx.commit("changeLetter",letter)
    } */
  }
})
