<template>
  <div class="detail">
    <img :src="bannerImg" alt @click="handleClick">
    <div>
      <detail-nav v-if="isNav" :style="opacitys"></detail-nav>
      <router-link v-else tag="i" to="/" class="iconfont bg">&#xef07;</router-link>
    </div>
    <v-fade>
      <detail-gallary slot="fade" v-show="isGallary" @toggle="hide" :gallaryImgs="gallaryImgs"></detail-gallary>
    </v-fade>
    <detail-list :categoryList="categoryList"></detail-list>
  </div>
</template>
<script>
import { DetailModel } from "../../models/DetailModel";
const detail = new DetailModel();
import DetailGallary from "../detail/components/gallary";
import DetailNav from "../detail/components/nabar";
import VFade from "../detail/components/fade";
import DetailList from "../detail/components/list"
export default {
  name: "detail",
  components: {
    DetailGallary,
    DetailNav,
    VFade,
    DetailList
  },
  data() {
    return {
      bannerImg: "",
      gallaryImgs: [],
      categoryList: [],
      isGallary: false,
      isNav: false,
      opacitys: {
        opacity: 0
      }
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    var id = this.$route.params.id;
    console.log(id);
    detail.getDetail(id).then(res => {
      let data = res.data.data;
      console.log(data);
      let bannerImg = data.bannerImg;
      this.bannerImg = bannerImg;
      let { gallaryImgs, categoryList } = data;
      this.gallaryImgs = gallaryImgs;
      this.categoryList = categoryList;
      
    });
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleClick() {
      this.isGallary = true;
    },
    hide() {
      this.isGallary = false;
    },
    handleScroll() {
      var top = document.documentElement.scrollTop || document.body.scrollTop;
      if (top > 0) {
        this.isNav = true;
        var opacity = top / 200;
        if (opacity > 1) {
          opacity = 1;
        }
        this.opacitys.opacity = opacity;
      } else {
        this.isNav = false;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.detail {
  height: 2000px;
  img {
    width: 100%;
  }
}
.bg {
  position: absolute;
  top: 0;
  left: 0;
  color: white;
  z-index: 3;
  font-size: 35px !important ;
  padding: 12px;
}
</style>