<template>
  <div class="wrapper" ref="wrapper">
    <div class="citylist">
      <div class="area">
        <div class="title">当前城市</div>
        <div class="content">
          <div class="btn current">{{this.$store.state.city}}</div>
        </div>
      </div>
      <div class="hold">
        <div class="title">热门城市</div>
        <div class="content">
          <div
            class="btn"
            @click="handleClick"
            v-for="(item,index) of hotCities"
            :key="index"
          >{{item.name}}</div>
        </div>
      </div>
      <div class="item-list" v-for="(item,index) of cities" :key="index">
        <div class="title" :ref="index">{{index}}</div>
        <div class="item"
        @click="handleClick"
        v-for="(city,inx) of item" :key="inx">{{city.name}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
  name: "CityList",
  props: ["hotCities", "cities"],
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
  computed: {
    letter() {
      return this.$store.state.letter;
    }
  },
  watch: {
    letter() {
      // console.log(1);
      const element = this.$refs[this.letter][0];
      this.scroll.scrollToElement(element);
    }
  },
  methods: {
    handleClick(event) {
      this.$store.commit("changeCity",event.target.innerText);
      this.$router.push("/")
    }
  }
};
</script>
<style lang="scss" scoped>
.wrapper {
  width: 100%;
  height: 1100px;
  overflow: hidden;
  position: relative;
  margin-top: 176px;
}
.citylist {
  font-size: 28px;
}
.title {
  background: #eee;
  height: 60px;
  color: #333;
  padding-left: 20px;
  line-height: 60px;
}
.btn {
  width: 200px;
  height: 54px;
  border-radius: 10px;
  border: 1px solid #eee;
  text-align: center;
  line-height: 54px;
}
.content {
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  grid-row-gap: 10px;
}
.item {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  height: 56px;
  border-bottom: 1px solid #eee;
  line-height: 56px;
  padding-left: 24px;
  color: #666;
}
.current {
  background: #ccc;
}
</style>
