<template>
<div>
  <div class="search">
    <input v-model="keyword" type="text" placeholder="输入城市名或拼音">
  </div>
  <div ref="wrapper" class="search-content" v-show="keyword">
      <!-- 只有有关键字的情况，就让search-content显示 -->
      <ul>
        <li @click="handleClick" v-for="(item,index) of list" :key="index">{{item.name}}</li>
        <!-- 搜索不到内容，就是list.length为0的情况，就显示以下内容 -->
        <li v-show="!list.length">没有你想要的城市</li>
      </ul>
    </div>
</div>
</template>
<script>
import BScroll from "better-scroll";
export default {
  name: "CitySearch",
  props: ["cities"],
  data() {
    return {
      keyword: "",
      /* list接收能够搜索到的数据 */
      list: []
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
  methods: {
    handleClick(event) {
      this.$store.commit("changeCity", event.target.innerText);
      this.$router.push("/");
      this.keyword=""
    }
  },
  watch: {
    keyword() {
      if (!this.keyword) {
        this.list = [];
        return;
      }
      var result = [];
      for (var i in this.cities) {
        this.cities[i].forEach(ele => {
          if (ele.name.startsWith(this.keyword) || ele.spell.startsWith(this.keyword)) {
            result.push(ele);
          }
        });
      }
      this.list = result;
    }
  }
};
</script>

<style lang="scss" scoped>
.search {
  z-index: 99;
  position: fixed;
  width: 100%;
  top: 89px;
  height: 88px;
  display: flex;
  background: #00bcd4;
  align-items: center;
  justify-content: center;
  input {
    border: 1px solid #eee;
    border-radius: 15px;
    outline: none;
    font-size: 14px;
    padding-left: 20px;
    height: 66px;
    width: 92%;
  }
}
.search-content {
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin-top: 200px;
  z-index: 400;
  background: #fff;
  overflow: hidden;
  height: 1155px;
  li {
    height: 56px;
    line-height: 56px;
    border-bottom: 1px solid #eee;
    padding-left: 24px;
    color: #666;
    font-size: 28px;
  }
}
</style>
